<template>
    <v-timeline>
      <v-timeline-item>
      
        <template v-slot:opposite>
        </template>
        <v-card class="elevation-2">
          <v-card-title class="headline">Abhimanyu Barge :- </v-card-title>
          <v-card-text>नळाला पाणी येत नसल्याची तक्रार — सोयगाव: शहरातील वार्ड क्रमांक पाचमध्ये दोन वर्षांपासून नळाला पाणी येत नाही. नागरिकांनी अनेकवेळा पाठपुरावा करून देखील नगर पंचायत दुर्लक्ष करीत आहे. वारंवार चकरा मारणाऱ्या नागरिकांना कर्मचारी उद्धट वागणूक देत असल्याचा आरोप आहे. वाल्मिकनगर भागातील २० ते २५ नळांना दोन वर्षांपासून पाणी येत नाही. जलकुंभापासून लांब असलेल्या या भागात पाणी पोहचत नसल्याने पाण्यासाठी भटकावे लागत आहे. शेजारच्या घरांना मुबलक पाणी मिळते. याबाबत अनेक वेळा नगर पंचायतमध्ये तक्रारी केल्या आहेत. मुख्याधिकारी, नगराध्यक्ष व नगरसेवक कोणीही या प्रश्नाकडे लक्ष देत नसल्याचा आरोप आहे. येथील कर्मचारी शिंदे हे आरेरावी करत असल्याची तक्रार नागरिकांनी बुधवारी मुख्याधिकारी यांच्याकडे केली. हा प्रश्न तत्काळ सोडवला नाही, तर आंदोलनाचा इशारा दिला आहे. या निवेदनावर दीपक पाटील, बापू बावस्कर, अशोक घनघाव, वसंत खेडकर, प्रभाकर घनघाव, शांताराम बावस्कर, गजानन काकडे, हरीभाऊ जाधव, सुरेश सोनवणे, सुपडू घनघाव, योगेश मानकर, अमोल बडगुजर आदींच्या सह्या आहेत.</v-card-text>
        </v-card>
      </v-timeline-item>

      <v-timeline-item>
      
      <template v-slot:opposite>
      </template>
      <hr>
      <v-card class="elevation-2">
        <v-card-title class="headline">प्रशासन क्रिया :- </v-card-title>
        <v-card-text>शहरातील वार्ड क्रमांक पाचमध्ये दोन वर्षांपासून नळाला पाणी येत . नागरिकांनी अनेकवेळा पाठपुरावा करून देखील नगर पंचायत दुर्लक्ष करीत आहे</v-card-text>
      </v-card>
    <hr>

      <v-card class="elevation-2">
        <v-card-title class="headline">कृपया तुमच्या प्रतिक्रिया व्हिडिओ स्वरूपात कळवा  </v-card-title>
       <v-card-text>
        <input type="file" v-on:change="handleFileInputChange">
      </v-card-text>
      
         
     
   
      </v-card>

    </v-timeline-item>
  
    </v-timeline>
  </template>

  

<script>
  export default {
    name: 'querySummery',
    created(){
      this.$store.state.openNevigation = false
    },
    data () {
      return {
        items: [
        {
          color: 'red lighten-2',
          icon: 'mdi-star'
        },
        {
          color: 'purple darken-1',
          icon: 'mdi-book-variant'
        },
        {
          color: 'green lighten-1',
          icon: 'mdi-airballoon'
        },
        {
          color: 'indigo',
          icon: 'mdi-buffer'
        }
      ]
      }
    },
    methods: {
      handleFileInputChange(event) {
      // Access the file through event.target.files
      const file = event.target.files[0];
      
      // Do whatever you need with the file
      console.log('Selected file:', file);
    }
    }
  }
</script>


